import styles from "./Todo.module.scss";
import classNames from "classnames/bind";
import {
  Box,
  Button,
  Checkbox,
  IconButton,
  Stack,
  TextField,
} from "@mui/material";
import { useState } from "react";
import DeleteIcon from "@mui/icons-material/Delete";

const cx = classNames.bind(styles);

function Todo() {
  const [job, setJob] = useState("");
  const [todos, setTodos] = useState([]);

  const handleSubmit = () => {
    if (job !== "") {
      setTodos([...todos, { title: job, completed: false }]); // Sa i  mng todos bao gm i tng c title v trng thi hon thnh (completed) ban u l false
      setJob("");
    }
  };

  const handleCompleted = (index) => {
    const updatedTodos = [...todos]; // To bn sao ca mng todos bng cch s dng ton t spread
    updatedTodos[index].completed = !updatedTodos[index].completed; // o ngc trng thi hon thnh ca cng vic c ch s index
    setTodos(updatedTodos); // Cp nht li mng todos vi cng vic  c nh du hon thnh
  };
  const handleRemove = (index) => {
    const removedArr = [...todos].filter((todo, i) => index !== i);
    setTodos(removedArr); // Cp nht li mng todos vi cng vic  c nh du hon thnh
  };

  return (
    <Box
      width={600}
      height={760}
      backgroundColor={"#f6f9fc"}
      borderRadius={4}
      mt={4}
    >
      <div
        style={{
          display: "flex",
          justifyContent: "center",
        }}
      >
        <h1 className={cx("title")}>Todo-list</h1>
      </div>
      <Stack
        spacing={4}
        direction="row"
        justifyContent="center"
        alignItems="center"
      >
        <TextField
          sx={{ width: 400 }}
          value={job}
          id="standard-basic"
          label="What do you need to do today ?"
          variant="standard"
          onChange={(e) => {
            setJob(e.target.value);
          }}
        />

        <Button variant="contained" onClick={handleSubmit}>
          ADD
        </Button>
      </Stack>

      <Box sx={{ width: "100%" }}>
        <ul className={cx("list")}>
          {todos.map((job, index) => {
            return (
              <li
                style={{ display: "flex", justifyContent: "space-between" }}
                key={index}
                className={cx({
                  red: job.completed, // Nu cng vic  c nh du hon thnh (completed = true) th s dng lp CSS "red"
                  blue: !job.completed, // Ngc li, s dng lp CSS "blue"
                })}
              >
                <div>
                  {job.completed ? (
                    <Checkbox disabled checked />
                  ) : (
                    <Checkbox
                      checked={job.completed} // t trng thi hon thnh ca Checkbox bng vi trng thi ca cng vic
                      onChange={() => handleCompleted(index)} // Khi Checkbox c thay i trng thi, gi hm handleCompleted  cp nht trng thi hon thnh ca cng vic tng ng
                    />
                  )}
                  {job.title}
                </div>
                <div>
                  {job.completed ? (
                    <IconButton aria-label="delete" disabled>
                      <DeleteIcon />
                    </IconButton>
                  ) : (
                    <IconButton
                      aria-label="delete"
                      onClick={() => handleRemove(index)}
                    >
                      <DeleteIcon />
                    </IconButton>
                  )}
                </div>
              </li>
            );
          })}
        </ul>
      </Box>
    </Box>
  );
}

export default Todo;
